// Updated pagination.component.ts
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {
  @Output() onPageChange = new EventEmitter();
  @Input() page: number = 1;
  @Input() limit: number = 10;
  @Input() totalDocs: number = 0;

  constructor() { }

  ngOnInit() {
    // Ensure page is at least 1
    if (this.page < 1) {
      this.page = 1;
    }
  }

  pageChange(page: number) {
    // Validate page number
    const maxPage = Math.ceil(this.totalDocs / this.limit);
    if (page < 1) {
      page = 1;
    } else if (page > maxPage) {
      page = maxPage;
    }
    
    this.page = page;
    this.onPageChange.emit({ page: this.page, limit: this.limit });
  }

  limitChange() {
    // Reset to first page when changing limit to avoid issues
    this.page = 1;
    this.onPageChange.emit({ page: this.page, limit: this.limit });
  }

  // Helper method to get total pages (useful for debugging)
  get totalPages(): number {
    return Math.ceil(this.totalDocs / this.limit);
  }
}